










































import Vue from "vue";
import Component from "vue-class-component";
import Swipe from "../../components/Swipe.vue";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import { Toast } from "vant";


@Component({
  components: {
    Swipe
  },
  mixins: [mixin]
})
export default class Category extends Vue {
  catList = [];
  selectIndex = 0;
  catContent = [];
  value='';

  @Action("setTabIndex") setTabIndex;

  mounted() {
    this.setTabIndex(1);

    console.log(this.$store.getters[Vue.prototype.MutationTreeType.TAB_INDEX]);
    this.getCatList();
        if (
      this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO].userId !=
        "" &&
      this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO].token != ""
    ) {
      this.getCartList()
    }
    console.log("分类页加载");
  }
   getNumber(cartList = []) {
    let num = 0;
    cartList.forEach((item, index) => {
      num += item.num;
    });
    return num.toString();
  }
  getCartList() {
    Vue.prototype.$reqFormPost(
      "/shop/cart/query",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        var div = document.getElementById("cartLen");
        if (!document.getElementById("cartLen")) {
          div = document.createElement("div");
          div.setAttribute("id", "cartLen");
          div.className = "messageFexid";
          div.style.right = "11px";
          var diva = document.getElementsByClassName(
            "van-tabbar-item__text"
          )[2];
          diva.appendChild(div);
        }
        if (res.data.data.carts.length > 0) {
          div.innerHTML = this.getNumber(res.data.data.carts);
        } else {
          div.style.display = "none";
        }
      }
    );
  }
    filterProduct() {
    this.$router.push("/filterproduct");
  }
  goProductList(catId,parentCatId,selectIndex) {
    
    this.$router.push({
      path: "/productlist",
     query:{
       catId:catId,
       parentCatId,
       selectIndex,
       title: this.catList[this.selectIndex].catName
       }
    });
  }
  selectCategoyItem(index) {
    this.selectIndex = index;
    this.catContent = this.catList[index].children;
  }
  getCatList() {
    Vue.prototype.$reqFormPost("/user/cat/querytree", {}, res => {
      if (res == null) {
        console.log("网络请求错误！");
        return;
      }
      if (res.data.status != 200) {
        console.log(
          "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
        );
        Toast(res.data.message);
        return;
      }
      console.log("selectCategoyItem",res.data.data.children);
      this.catList = res.data.data.children;
      this.selectCategoyItem(this.selectIndex);
    });
  }
}



